import React from 'react'
import { Carousel } from 'antd';

import Images from './sliderImages/images';

const Home = () => {
  return (
    <div>
      <Carousel autoplay>
        {Images.map(image => (
          <>
            <div
              style={{
                'background-image': `url(${image.path})`,
                height: '400px',
                width: '100%',
                'background-size': 'cover',
              }}
            />
            <h3
              style={{
                position: 'relative',
                color: '#fff',
                'text-shadow': '1px 1px 2px rgba(0,0,0,.6)',
                top: '-60px',
                right: '20%',
                'font-size': '24px',
                'font-weight': 'bold',
              }}
            >
              image {image.id}
            </h3>
          </>
        ))}
      </Carousel>
    </div>
  )
}

export default Home;
