import React, { Component } from 'react'
import en from './en.png'
import ka from './ka.png'

class LanguageToggler extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activeLanguage: ''
    };

    this.setActiveLanguage = this.setActiveLanguage.bind(this);
  }

  componentDidMount(){
    this.setState({activeLanguage: localStorage.getItem('activeLanguage') || 'en'});
    const { activeLanguage } = this.state;
    localStorage.setItem('activeLanguage', activeLanguage);
  }

  setActiveLanguage(e){
    const { activeLanguage } = this.state;
    this.setState({activeLanguage: activeLanguage === 'en' ? 'ge' : 'en'});

    localStorage.setItem('activeLanguage', activeLanguage);
  }
  
  render(){
    const { activeLanguage } = this.state;
    return (
      <img
        style={{width: '18px', height: '18px', cursor: 'pointer', marginTop: '-4px'}}
        src={activeLanguage === 'en' ? en : ka } alt={activeLanguage}
        onClick={e => this.setActiveLanguage(e)}
      />
    )
  }
}

export default LanguageToggler;
